<template>
    <div>
        <div class="page">
            <!--search-header-->
            <div class="search-header">
                <div class="search-header-upper">
                    <div class="upper-back" @click="$router.push('/')"></div>
                    <div class="upper-search" @click="$router.push('/goods/search')">
                        <div class="upper-search-icon"></div>
                        <div class="upper-search-text">搜索</div>
                    </div>
                    <div class="upper-screen" @click="isHide =!isHide">筛选</div>
                </div>
                <div class="search-header-main">
                    <div class="main-item" @click="isUp = !isUp">
                        <div :class="{'main-item-text':true, up:isUp}">综合</div>
                        <div :class="{'main-item-icon':true, up:isUp}"></div>
                        <ul :class="{'main-item-menu':true,up:isUp}">
                            <li v-for="(item1,index1) in sortList" @click="onSortList(index1)" :key="index1">{{item1.title}}</li>
                        </ul>

                    </div>
                    <div class="main-item" @click="selectUp">
                        <div :class="{'main-item-text':true, up:isUps}">销量</div>
                    </div>
                </div>
            </div>
            <!--search-content-->
            <div class="search-content" >
                <div class="content-list"  v-for="(item,index) in productData" :key="index" @click="$router.push('/goods/details/item?gid='+item.gid)">
                    <div class="content-list-image">
                        <img :src="item.image" :alt="item.title"/>
                    </div>
                    <div class="content-list-content">
                        <div class="content-title">{{item.title}}</div>
                        <div class="content-price">¥{{item.price}}</div>
                        <div class="content-sales">销量 <span>{{item.sales}}</span> 件</div>
                    </div>
                </div>
                <div  v-show="productData.length<=0" class="no-data" >没有相关商品！</div>
            </div>

            <!--hide-screnn-->
            <div :class="{'hide-screnn':true ,hide:!isHide}" @click="isHide=!isHide"></div>
            <!--
            search-screen
            -->
            <div :class="{'search-screen':true,hide:!isHide}">
                <div class="screen-wrap">
                    <div class="screen-title" @click="isClassifyActive =!isClassifyActive">
                        <div class="screnn-title-text">分类</div>
                        <div :class="{'screnn-title-icon':true ,active:!isClassifyActive}"></div>
                    </div>
                    <div :class="{'screen-item': true,itemHide:isClassifyActive}">
                        <div v-for="(item,index)  in classifyData" @click="classifyStyle(index)"
                             :class="{item:true, active:item.active}" :key="index">{{item.title}}
                        </div>
                    </div>
                </div>
                <!--价格区间-->
                <div class="screen-wrap">
                    <div class="screen-title" @click="isPriceActive =!isPriceActive">
                        <div class="screnn-title-text">价格区间</div>
                        <div class="screnn-title-price" @click.stop>
                            <div class="price-min">
                                <input type="number" placeholder="最低价" :value="minPrice" @input="MIN_PRICE({minPrice:$event.target.value})">
                            </div>
                            <div class="price-line"></div>
                            <div class="price-min">
                                <input type="number" placeholder="最高价" :value="maxPrice" @input="MAX_PRICE({maxPrice:$event.target.value})">
                            </div>
                        </div>
                        <div :class="{'screnn-title-icon':true,active:isPriceActive}"></div>
                    </div>
                    <div :class="{'screen-item':true,actives:!isPriceActive}">
                        <div :class="{item:true,active:item.active}"
                             v-for="(item,index) in priceData.items"
                             @click="SET_PRICESORT({index:index})" :key="index">{{item.price1}}-{{item.price2}}</div>
                    </div>
                </div>
                <!--品牌-->
                <div class="screen-wrap" v-for="(item,index) in attrs" :key="index">
                    <div class="screen-title" @click="isScrennActive =!isScrennActive" >
                        <div class="screnn-title-text">{{item.title}}</div>
                        <div :class="{'screnn-title-icon':true,active:isScrennActive}"></div>
                    </div>
                        <div :class="{'screen-item':true,activess:!isScrennActive}">
                        <div
                             v-for="(item2,index1) in item.param" :key="index1"
                             :class="{item:true,active:item2.active}"
                             @click="SET_ATTRS_STYLE({index:index,index1:index1})"> {{item2.title}}</div>
                    </div>
                </div>
                <!-- handel-wrap-->
                <div data-v-b8cf3068="" class="handel-wrap">
                    <div data-v-b8cf3068="" class="item">共<span data-v-b8cf3068="">{{total}}</span>件</div>
                    <div data-v-b8cf3068="" class="item reset" @click="getAttributeReset">全部重置</div>
                    <div data-v-b8cf3068="" class="item sure" @click="sumitTo()">确定</div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
    import {mapActions, mapState, mapMutations} from "vuex"
    import UpRefresh from '../../assets/js/libs/uprefresh';
    export default {
        data() {
            return {
                kwords: this.$route.query.kwords ? this.$route.query.kwords : "",
                //分类显示隐藏
                isClassifyActive: false,
                //价格区间显示隐藏
                isPriceActive: false,
                //品牌显示隐藏
                isScrennActive: false,
                //筛选页面整体隐藏
                isHide: false,
                //综合显示隐藏
                isUp: false,
                //销量显示隐藏
                isUps: false,
                //综合数组
                sortList:[
                    {otype:"all",title:"综合"},
                    {otype:"up",title:"从低到高"},
                    {otype:"down",title:"从高到底"}
                ]
            }
        },
        computed: {
            ...mapState({
                //左侧导航数据
                classifyData: (state) => state.goods.classifyData,
                //产品搜索结果数据
                productData: (state) => state.search.productData,
                //获取cid
                cid: (state) => state.search.cid,
                //获取title
                title: (state) => state.search.title,
                //获取价格区间priceData
                priceData:(state)=>state.search.priceData,
                //最小价格
                minPrice:(state)=>state.search.minPrice,
                //最大价格
                maxPrice:(state)=>state.search.maxPrice,
                //商品属性
                attrs:(state)=>state.search.attrs,
                //设置商品属性pid
                params:(state)=>state.search.params,
                //商品总件数
                total:(state)=>state.search.total

            })
        },
        created() {
            //创建分页实例
            this.pullUp=new UpRefresh();
            //商品属性
            this.getAttribute({kwords:this.kwords});
            //网易头部着改变
            document.title = this.$route.meta.title;
            this.kwords = this.$route.query.kwords;
            console.log(this.kwords );
            this.init();
            //运行左侧导航方法
            this.getClassify();
            //选择分类
            //this.selectClassify()
        },
        methods: {
            //销量排序
            selectUp() {
                this.isUps = true;
                this.otype = "sales";
                this.init();
            },
            init(){
                let searchData = {kwords: this.kwords?this.kwords:"",
                    cid:this.cid,otype:this.otype,
                    price1:this.minPrice,price2:this.maxPrice,param:JSON.stringify(this.params)};
                this.productSearch({...searchData,success:(pageNum)=>{

                    //分页功能
                        this.pullUp.init({"curPage":1,"maxPage":parseInt(pageNum),"offsetBottom":100},(page)=>{
                            this.getSearchPage({...searchData,page:page});
                        });
                    }})
            },

            //价格排序
            onSortList(index){
                if (this.sortList.length>0){
                    for (let i = 0;i<this.sortList.length;i++){
                    }
                    this.otype = this.sortList[index].otype;
                    this.init()
                }

            },
            //提高筛选数据
            sumitTo(){
                this.isHide =false;
                this.SET_ATTRS_PID();
                this.init();
            },
            ...mapActions({
                //产品搜索结果数据
                productSearch: "search/productSearch",
                //左侧导航方法
                getClassify: "goods/getClassify",
                //选择分类
                selectClassify: "search/selectClassify",
                //商品属性
                getAttribute:"search/getAttribute",
                //商品重置
                getAttributeReset:"search/getAttributeReset",
                //分页功能
                getSearchPage:"search/getSearchPage"


            }),
            ...mapMutations({
                SELECT_ACTIVEITEM: "goods/SELECT_ACTIVEITEM",
                //价格排序
                SET_PRICESORT:"search/SET_PRICESORT",
                //最小价格
                MIN_PRICE:"search/MIN_PRICE",
                //最大价格
                MAX_PRICE:"search/MAX_PRICE",
                //设置商品属性
                SET_ATTRS_STYLE:"search/SET_ATTRS_STYLE",
                //设置商品属性的pid
                SET_ATTRS_PID:"search/SET_ATTRS_PID",

            })
            ,
            classifyStyle(index) {
                this.selectClassify({index: index});


            }
        },
        beforeDestroy() {
            this.pullUp.uneventSrcoll();
        },
        beforeRouteUpdate(to, from, next) {
            this.kwords =to.query.kwords
            this.otype = "all";
            this.init();
            next();
        }
    }
</script>

